@import 'color';
@import 'bg';
@import 'global';

// top bar
.top-bar {
	background: black;
	display: flex;
	align-items: center;

	.bar__item {
		text-align: center;
		flex-grow: 1; }
	.bar__logo {
		> p {
			font-size: 3em;
			margin: 20px 0px;
			span {
				font-weight: bold;
				color: $highlight; } } }
	.bar__hotline {}

	.bar__button {
		button {
			border: none;
			background: white;
			padding: 10px 20px; } } }
// main menu
.main-menu {
	ul {
		display: flex;
		list-style-type: none;
		margin: 10px 0px;
		text-align: center;
		padding: 0;
		justify-content: space-between;

		border-bottom: 1px solid $grey;

		li {
			flex-grow: 1;
			a {
				display: block;
				text-decoration: none;
				color: $text;
				padding: 20px 20px;

				transition: background-color 0.2s ease-in-out;
				&:hover {
					background: $highlight;
					color: white; } } } } }


// content
.content {
	display: flex;
	border-top: 1px solid $grey;
	margin-top: 10px; }

.content__left {
	background: green;
	width: 30%;
	flex-grow: 1; }

.content__right {
	flex-row: 1;
	width: 70%; }

.products {
	display: flex;
	flex-wrap: wrap;
	padding: -10px 0 0 -10px; }

.product__block {
	position: relative;
	z-index: 200;
	box-sizing: border-box;
	lost-column: 1/4 4 10px;
	margin-bottom: 10px; }

.block__top {
	position: relative;

	.block__image {
		position: relative;
		img {
			display: block; }
		.addition {
			height: 100%;
			position: absolute;
			left: 0;
			bottom: 0;
			opacity: 0;
			transition: opacity 0.3s ease-in-out; } }

	.block__button {
		width: 100%;
		position: absolute;
		bottom: 0;
		opacity: 0;
		transition: opacity 0.2s ease-in-out;

		button {
			width: 100%;
			border: none;
			background: $highlight;
			padding: 15px;
			text-transform: uppercase;
			font-weight: bold;
			color: white;
			&:hover {
				background: darken($highlight, 15%); } } }

	.block__function {
		position: absolute;
		left: 0;
		bottom: 35px; }

	.block__status {
		position: absolute;
		left: 0;
		top: 5px; } }

.product__block {
	&:hover {
		.block__button {
			opacity: 1;
			button {
				z-index: 5; } }
		.block__image {
			.addition {
				opacity: 1; } } } }




.block__bottom {
	z-index: 100; }
