@import 'color';

html, body {
	height: 100%; }

h1, h2, h3, h4, h5, h6 {
	color: $title-text; }

table {
	border-collapse: collapse; }


table, th, td {
	border: 1px solid $grey; }

td {
	padding: 10px 20px; }

div {
	color: $text; }

.wrap {
	max-width: 1200px;
	margin: 0 auto; }

.shadow {
	box-shadow: 0 1px 1px rgba(0,0,0,.05); }

.panel {
	background-color: white; }

.responsive-image {
	width: 100%;
	height: auto; }
